import * as fm from 'loopring-sdk';
import { getBaseQuote, MarketTradeInfo, Side, UserTrade } from 'loopring-sdk';
import { RawDataTradeItem } from '@loopring-web/component-lib/components/tableList/tradeTable/TradeTable';
import store from '../../stores';
import { TradeTypes } from '@loopring-web/component-lib/static-resource/index';
import { BIG10 } from '../../defs/swap_defs';
import { ammpoolAPI, userAPI } from '../../stores/apis/api';
import { AmmRecordRow, AmmTradeType } from '@loopring-web/component-lib/components/tableList/ammRecordTable/Interface';
import { AmmPoolTx, BillType, TxTypes, UserAmmPoolTx } from 'loopring-sdk';
import { CoinMap } from '@loopring-web/component-lib/static-resource';

export const getUserTrades = (marketKey: any) => {
    const userApi = userAPI();
    const {accountId, apiKey} = store.getState().account
    return userApi.getUserTrades({accountId}, apiKey).then((response: {
        totalNum: any;
        userTrades: UserTrade[];
        raw_data: any;
    }) => {
        return response.userTrades
    })
}
export const makeMarketArray = (coinKey: any, marketTrades: MarketTradeInfo[]): RawDataTradeItem[] => {

    let tradeArray: Array<Partial<RawDataTradeItem>> = []

    marketTrades.forEach((item: MarketTradeInfo) => {
        try {
            const {base, quote} = getBaseQuote(item.market)
            const {forex} = store.getState().system
            const {currency} = store.getState().settings
            const {tokenMap} = store.getState().tokenMap
            if (tokenMap) {
                const baseToken = tokenMap[ base as string ]
                const quoteToken = tokenMap[ quote as string ]

                tradeArray.push({
                    side: item.side === Side.Sell ? TradeTypes.Sell : TradeTypes.Buy,
                    amount: {
                        from: {
                            key: base as string,
                            value: fm.toBig(item.volume).div(BIG10.pow(baseToken.decimals)).toNumber(),
                        },
                        to: {
                            key: quote as string,
                            value: fm.toBig(item.price).times(item.volume).div(BIG10.pow(quoteToken.decimals)).toNumber(),
                        },
                    },
                    price: {
                        key: '',
                        value: fm.toBig(item.price).toNumber(),
                    },
                    fee: {
                        key: '',
                        value: fm.toBig(item.fee).div(BIG10.pow(quoteToken.decimals)).toNumber(),
                    },
                    time: parseInt(item.tradeTime.toString()),
                })
            }
        } catch (error) {
            //CATCHERROR:
            console.log(error)
            // new CustomError()
        }

    })
    // console.log('tradeArray:', tradeArray)
    return tradeArray as RawDataTradeItem[];

}

export const getUserAmmTransaction = (marketKey: any) => {
    const {getUserAmmPoolTxs} = ammpoolAPI();
    const {accountId, apiKey} = store.getState().account
    return getUserAmmPoolTxs({accountId}, apiKey).then(({userAmmPoolTxs}) => {
        return userAmmPoolTxs
    })
}

export const makeMyAmmMarketArray = <C extends object>(coinKey: any, marketTransaction: UserAmmPoolTx[]): AmmRecordRow<C>[] => {

    let tradeArray: Array<Partial<AmmRecordRow<C>>> = [];

    marketTransaction.forEach((item: UserAmmPoolTx) => {
        try {
            const {base, quote} = getBaseQuote(coinKey)
            const {forex} = store.getState().system
            // const {currency} = store.getState().settings
            const coinMap = store.getState().tokenMap.coinMap as CoinMap<C>
            if (coinMap) {
                tradeArray.push({
                    type: item.txType === TxTypes.JOIN ? AmmTradeType.add : AmmTradeType.remove,
                    //TODO:
                    totalDollar: 1000,
                    totalYuan: 1000 / Number(forex),
                    amountA: Number(item.poolTokens[ 0 ].amount),
                    amountB: Number(item.poolTokens[ 1 ].amount),
                    time: Number(item.updatedAt),
                    coinA: coinMap[ base as keyof C ],
                    coinB: coinMap[ quote as keyof C ],
                })
            }
        } catch (error) {
            //CATCHERROR:
            console.log(error)
            // new CustomError()
        }

    })
    // console.log('tradeArray:', tradeArray)
    return tradeArray as AmmRecordRow<C>[];

}


