import React from "react";
import { AmmData, AmmInData, CoinInfo, IBData } from '@loopring-web/component-lib/src/static-resource';
import { AmmPanelType } from '@loopring-web/component-lib';
import { useTokenMap } from '../../../stores/token';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import { ammPairInit } from '../../../hooks/help/ammPairDetail';
import {  WalletMap } from '@loopring-web/component-lib/static-resource';
import { AmmPoolSnapshot, TickerData } from 'loopring-sdk';

export const useAmmPanel = <C extends { [ key: string ]: any }>({
                                                                    pair,
                                                                    walletMap,
                                                                    ammType,
                                                                    ammpoolsbalance,
                                                                    tickerData
                                                                }
                                                                    : {
    pair: { coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined },
    // ammCalcData:AmmInData<C>,
    ammpoolsbalance:AmmPoolSnapshot,
    walletMap: WalletMap<C>
    tickerData: TickerData | undefined,
    ammType: keyof typeof AmmPanelType
}) => {
    // const walletLayer2State = useWalletLayer2();
    const {coinMap, tokenMap} = useTokenMap();
    const {ammMap} = useAmmMap();

    const [ammCalcData, setAmmCalcData] = React.useState<AmmInData<C> | undefined>();
    const [ammDepositData, setAmmDepositData] = React.useState<AmmData<IBData<C>, C>>({
        coinA: {belong: undefined} as unknown as IBData<C>,
        coinB: {belong: undefined} as unknown as IBData<C>,
        slippage: .5
    } as AmmData<IBData<C>, C>);
    const [ammWithdrawData, setAmmWithdrawData] = React.useState({
        coinA: {belong: undefined} as unknown as IBData<C>,
        coinB: {belong: undefined} as unknown as IBData<C>,
        slippage: .5
    } as AmmData<IBData<C>, C>);
    const [ammBtnI18nKey, setAmmBtnI18nKey] = React.useState<string | undefined>(undefined);

    const handleDepositEvent = React.useCallback((data: AmmData<IBData<C>>, type: 'coinA' | 'coinB') => {
        console.log('handleDepositEvent', data, type);
    }, []);

    const handleWithDrawEvent = React.useCallback((data: AmmData<IBData<C>>, type: 'coinA' | 'coinB') => {
        console.log('handleDepositEvent', data, type);
    }, []);

    const onAmmWithdrawClick = React.useCallback((data) => {
        console.log('onAmmWithdrawClick', data);
    }, []);
    const onAmmDepositClick = React.useCallback((data) => {
        console.log('onAmmDepositClick', data);
    }, []);


    React.useEffect(() => {
        if (tickerData) {
            initAmmData(pair)
        }
    }, [tickerData,walletMap]);
    const initAmmData = async (pair: any) => {

        let _ammCalcData: Partial<AmmInData<C>> = ammPairInit(
            {
                pair,
                ammType,
                ammpoolsbalance,
                _ammCalcData: {},
                tokenMap,
                coinMap,
                walletMap: walletMap, //walletLayer2State.walletLayer2,
                ammMap,
                tickerData,
            })
        // let _tradeFloat: Partial<TradeFloat> = {}
        // let _tradeArray: Array<Partial<RawDataTradeItem>> | undefined = undefined;
        // const walletMap =  walletLayer2State.walletLayer2? updateWallet().walletMap:undefined;
        //AtoB
        setAmmCalcData({...ammCalcData, ..._ammCalcData} as AmmInData<C>);
        if (_ammCalcData.myCoinA) {
            if (ammType === AmmPanelType.Deposit) {
                setAmmDepositData({
                    coinA: _ammCalcData.myCoinA as IBData<C>,
                    coinB: _ammCalcData.myCoinB as IBData<C>,
                    slippage: 0.5
                })
            } else {
                setAmmWithdrawData({
                    coinA: _ammCalcData.myCoinA as IBData<C>,
                    coinB: _ammCalcData.myCoinB as IBData<C>,
                    slippage: 0.5
                })
            }


        }


    }

    // React.useEffect(() => {
    //     switch (walletLayer2State.status) {
    //         case "ERROR":
    //             walletLayer2State.statusUnset();
    //             //TODO: show error at button page show error  some retry dispat again
    //             break;
    //         case "DONE":
    //             walletLayer2State.statusUnset();
    //             const {walletMap} = updateWallet();
    //             initAmmData(pair)
    //             break;
    //         default:
    //             break;
    //
    //     }
    // }, [walletLayer2State.status])


    return {
        ammCalcData,
        ammDepositData,
        ammWithdrawData,
        handleDepositEvent,
        handleWithDrawEvent,
        onAmmWithdrawClick,
        onAmmDepositClick,
        ammBtnI18nKey
    }
}