import { AmmPanel, AmmPanelType } from '@loopring-web/component-lib';
import { AmmData, AmmInData, CoinInfo, IBData, WalletMap } from '@loopring-web/component-lib/static-resource';
import { useAmmPanel } from './hooks';
import React from 'react';
import { Box } from '@material-ui/core';
import { AmmPoolSnapshot, TickerData } from 'loopring-sdk';

export const AmmPanelView = <T extends AmmData<C extends IBData<I> ? C : IBData<I>>, I,
    ACD extends AmmInData<I>,
    C = IBData<I>>({
                       pair,
                       walletMap,
                       ammType,
                       ammpoolsbalance,
                       tickerData,
                       ...rest
                   }: {
    pair: { coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined },
    tickerData: TickerData | undefined,
    ammpoolsbalance:AmmPoolSnapshot,
    walletMap: WalletMap<C>
    ammType?: keyof typeof AmmPanelType
} & any) => {
    const {
        ammCalcData,
        ammDepositData,
        ammWithdrawData,
        handleDepositEvent,
        handleWithDrawEvent,
        onAmmWithdrawClick,
        onAmmDepositClick
    } = useAmmPanel({pair,ammpoolsbalance, walletMap, ammType: ammType ? ammType : AmmPanelType.Deposit, tickerData})

    // const [index, setIndex] = React.useState(AmmPanelTypeMap[ tabSelected ]);

    return <> {tickerData ?
        <AmmPanel {...{...rest}}
                  ammWithdrawData={ammWithdrawData}
                  ammDepositData={ammDepositData}
                  ammCalcData={ammCalcData}
                  handleDepositEvent={handleDepositEvent}
                  handleWithDrawEvent={handleWithDrawEvent}
                  onAmmWithdrawClick={onAmmWithdrawClick}
                  onAmmDepositClick={onAmmDepositClick}
                  tabSelected={ammType ? ammType : AmmPanelType.Deposit}

        /> : <Box width={'var(--swap-box-width)'}/>}
    </>

}