import { AmmpoolAPI, ChainId, ExchangeAPI, UserAPI, WsAPI } from "loopring-sdk"
import { getChainId } from 'stores/trading/hook'

export const userAPI = () => {
    const userApi = new UserAPI(getChainId() as ChainId)
    return userApi
}

export const exchangeAPI = () => {
    const exchangeApi = new ExchangeAPI(getChainId() as ChainId)
    return exchangeApi
}

export const ammpoolAPI = () => {
    const ammpoolApi = new AmmpoolAPI(getChainId() as ChainId)
    return  ammpoolApi
}

export const wsAPI = () => {
    const wsApi = new WsAPI(getChainId() as ChainId)
    return wsApi
}

export class LoopringAPI {
    
    public static userApi = new UserAPI(getChainId() as ChainId) 
    public static exchangeAPI = new ExchangeAPI(getChainId() as ChainId) 
    public static ammpoolAPI = new AmmpoolAPI(getChainId() as ChainId) 
    public static wsAPI = new WsAPI(getChainId() as ChainId) 

    public static setChainId = (chainId: ChainId) => {
        LoopringAPI.userApi.setChainId(chainId)
        LoopringAPI.exchangeAPI.setChainId(chainId)
        LoopringAPI.ammpoolAPI.setChainId(chainId)
        LoopringAPI.wsAPI.setChainId(chainId)
    }

}

